body{
  background-color:#5F7A61;
}
.main{
  background-color: #444941;
  min-height: 500px;
  margin: 40px auto;
  width: 400px;
  border-radius: 5px;
  position:absolute;
  margin-left: 450px;
}

#todo-form input{
  background-color: #7FC8A9;
  border:none;
  width: 200px;
  height:50px;
  padding:0 20px;
  margin:20px;
  border-radius:2px;
  font-size:20px;
  color:white;
}
#todo-form input::placeholder{
  color :white;
  font-size:15px
}

#todo-form button{
    height:50px;
    width: 80px;
    border-radius: 5px;
    border: none;
    background-color:#368B85;
    cursor:pointer;
    font-weight: border;
    color:white;
    font-size:20px;
} 

.list{
  color: #fff;
  width: 300px;
  margin: 20px auto;
  height: 50px;
  background-color: #6E85B2;
  border-radius: 5px;
  /* margin-bottom: 100px; */
}
.list p{
  padding: 10px;
  position: relative;
  transition: ease-in-out;
}
.list p span{
  position: absolute;
  right: 10px;
}
.faicons{
  margin: 0 5px;
  font-size: 15px;
  cursor: pointer;
}

.list p input{
  background-color: transparent;
  border: 0;
  color: #fff;
  font-size: 18px;
}
.list p input:focus{
  outline: none;
}
.heading{
  text-align: center;
  color :white;
  position: relative;
  margin-top: 20px;
}
